@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: fallback;
  src: url("../../fonts/openSans-Light") format("woff2"), url("../../fonts/openSans-Light") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: fallback;
  src: url("../../fonts/openSans-Regular") format("woff2"), url("../../fonts/openSans-Regular") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-display: fallback;
  src: url("../../fonts/openSans-SemiBold") format("woff2"), url("../../fonts/openSans-SemiBold") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: fallback;
  src: url("../../fonts/openSans-Bold") format("woff2"), url("../../fonts/openSans-Bold") format("woff"); }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

a {
  text-decoration: none;
  color: #000000; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  height: 100%;
  font-family: "Open Sans", "Arial", "sans-serif";
  font-size: 22px;
  font-weight: 400;
  line-height: 34px; }

.page-body {
  margin: 0; }

.main-nav {
  margin-bottom: 265px; }

.main-nav__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none; }

.main-nav__item {
  margin-right: 134px; }

.main-nav__link {
  font-weight: 300;
  font-size: 16px;
  color: #ffffff;
  opacity: 0.7; }

.container {
  margin: 0 auto;
  padding-left: 157px;
  padding-right: 157px;
  max-width: 1440px; }

.bike-info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.bike-info__description {
  max-width: 393px; }

.bike-info__description h2 {
  font-size: 46px;
  font-weight: 700;
  line-height: 64px;
  color: #ffffff; }

.bike-info__description p {
  font-size: 20px;
  font-weight: 300;
  line-height: 32px;
  color: #ffffff;
  opacity: 0.8; }

.bike-info__choice {
  max-width: 330px; }

.bike-choice-form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 20px; }

.bike-choice-form p {
  margin: 0;
  font-size: 17px;
  font-weight: 600;
  line-height: 28px;
  text-align: center;
  color: #ffffff; }

.bike-info__choice button {
  font-size: 15px;
  font-weight: 600;
  padding-top: 20px;
  padding-right: 48px;
  padding-bottom: 20px;
  padding-left: 48px;
  background-color: #0ad9c6;
  border: none;
  border-radius: 6px;
  cursor: pointer; }

.bike-info__choice input {
  width: 100%;
  font-size: 15px;
  padding-top: 14px;
  padding-left: 29px;
  padding-bottom: 10px;
  color: #1c1c1c; }

.background-container--header {
  min-height: 825px;
  background-image: url("../../img/jpg/bg-bike-desktop@1x.jpg");
  background-color: #101010;
  background-position: center 100%;
  background-repeat: no-repeat; }
  @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
    .background-container--header {
      background-image: url("../../img/jpg/bg-bike-desktop@2x.jpg");
      background-size: 1440px 825px; } }
